.first_app {
  padding: 15px 0;
}
.first_app .first_app_logo a {
  font-size: 25px;
}

.first_app .first_app_content ul.faT li a {
  font-size: 16px;
  color: #0e2f47;
  padding-left: 25px;
  text-decoration: none;
}

.first_app .first_app_content ul.faT {
  margin-bottom: 5px;
}

.first_app .first_app_content ul.faT li a:hover,
.first_app .first_app_content ul.faB li a:hover {
  color: var(--prim-color);
}

.first_app .first_app_content ul.faT li:first-child a {
  font-weight: 600;
  padding-left: 0;
}

.first_app .first_app_content ul.faB li a {
  font-size: 16px;
  color: #000;
  padding-left: 25px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
}

.first_app .input-group input {
  width: 0;
  padding: 0;
  border: 0;
  transition: all 800ms ease-in-out;
}

.first_app .input-group a {
  padding-left: 0 !important;
}

.first_app .input-group:hover input {
  width: 150px;
  padding: 15px;
  border-radius: 25px;
}

.first_app .input-group:hover {
  border: 1px solid var(--prim-color);
  margin-left: 15px;
  border-radius: 25px;
}

.first_app .input-group-text {
  background: transparent;
  border: none;
  color: var(--prim-color);
  border-radius: 25px;
}

.first_app .input-group-text {
  background: transparent;
  border: none;
  color: var(--prim-color);
  border-radius: 25px;
  padding-right: 0px;
}
