.timer {
  background-color: #0e2f47;
  height: 32px;
}

.timer p {
  margin: 0;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
}

.timer p span {
  font-weight: 400;
  text-transform: lowercase;
}

.timer p.timerText {
  margin-right: 10px;
}

.timer p.timerText span {
  padding: 0 8px;
}

.timer .timerSep {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-name: go;
  animation-name: go;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  display: inline-block;
  text-align: center;
  padding: 0 5px;
}

@-webkit-keyframes go {
  0%,
  to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes go {
  0%,
  to {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}

.tickIcon {
  display: inline-block;
  fill: #42a90c;
  margin-top: 5px;
  margin-right: 0.5em;
  vertical-align: middle;
  width: 20px;
}

.tickIcon svg {
  vertical-align: initial;
}
