.bannerContainer {
  height: 568px;
  width: 100%;
  /* position: relative; */
}

.bannerContainer img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bannerNav {
  margin-top: -15px;
}

.bannerNav span {
  font-size: 15px;
  padding: 5px 15px;
  height: 32;
  background: #fff;
  border: 2px solid #ccc;
  border-radius: 15px;
  margin-right: 15px;
  cursor: pointer;
}

.bannerNav span:last-child {
  margin-right: 0px;
}

.bannerNav span:hover {
  border: 2px solid var(--prim-color);
}

.bannerNav .active {
  border: 2px solid var(--prim-color);
}
