.primBtn {
  padding: 16px 24px;
  border-radius: 4px;
  margin: 32px auto 0;
  background-color: var(--prim-color);
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  border: none;
  box-shadow: none;
  outline: 0;
  display: flex;
}

.primBtn:focus {
  outline: 0;
}

.primBtn:hover {
  background: #6ab9b9;
}
