.igImgCard {
  position: relative;
  width: 255px;
  height: 255px;
  cursor: pointer;
}
.igImgCard img {
  width: 100%;
  height: 100%;
}
.igImgCard:hover .igImgInfo {
  opacity: 1;
}
.igImgCard .igImgInfo {
  transition: all 500ms ease-in-out;
  position: absolute;
  background: #ffffffba;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.igImgCard .iibHeart {
  font-size: 60px;
  margin: 15px 0;
}
.igImgCard p {
  font-size: 20px;
  color: #111111;
  font-weight: 600;
}

.igImgCard .iibLikes span {
  padding-right: 10px;
}
