.prodCnt {
  padding: 16px;
  width: calc(100 / 3);
  max-width: 400px;
}

.prodCard {
  border: 1px solid #d5dbde;
  display: block;
  transition: transform 0.25s;
  will-change: transform;
  cursor: pointer;
}

.prodCard:hover {
  box-shadow: 0 4px 32px 0 rgba(50, 50, 50, 0.25);
  transform: scale(1.01);
}

.prodInfo {
  padding: 16px 0;
}

.prodCard h3 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  color: #0e2f47;
}

.prodCard p {
  font-size: 16px;
  color: #0e2f47;
  margin-top: 0px;
  margin-bottom: 0;
}

.prodCard img {
  width: 100%;
}
